.content {
  max-width: 10rem;
  width: 100%;
  margin: 0 auto;
}

.content .banner {
  position: relative;
}

.content .banner a {
  display: block;
  width: 2.226667rem;
  height: 0.626667rem;
  background: url(../imgs/ali-constellation/btn.png);
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-animation: discount 3s infinite;
  animation: discount 3s infinite;
}

.content .banner .btn01 {
  position: absolute;
  right: 7.5%;
  top: 29%;
}

.content .banner .btn02 {
  position: absolute;
  right: 23%;
  top: 38.5%;
}

.content .banner .btn03 {
  position: absolute;
  left: 20%;
  top: 55.5%;
}

.content .banner .btn04 {
  position: absolute;
  right: 23%;
  top: 73.5%;
}

.content .banner .btn05 {
  position: absolute;
  left: 19%;
  bottom: 6%;
}

.rule {
  max-width: 10rem;
  width: 100%;
  color: #727171;
  background-color: #dcdcdc;
  overflow: hidden;
}

.rule dl {
  position: relative;
  margin: 0 auto;
}

.rule dl dt {
  padding-top: 0.533333rem;
  font-weight: bold;
  text-align: center;
}

[data-dpr="1"] .rule dl dt {
  font-size: 12px;
}

[data-dpr="2"] .rule dl dt {
  font-size: 24px;
}

[data-dpr="3"] .rule dl dt {
  font-size: 36px;
}

.rule dl dd {
  margin: 0.133333rem 0;
  text-align: center;
}

[data-dpr="1"] .rule dl dd {
  font-size: 10px;
}

[data-dpr="2"] .rule dl dd {
  font-size: 20px;
}

[data-dpr="3"] .rule dl dd {
  font-size: 30px;
}

.rule dl .alibglogo {
  display: block;
  width: 100%;
  height: 1.053333rem;
  background: url("../imgs/ali-cancer/ali-logo.png") no-repeat;
  background-size: 1.333333rem 1.053333rem;
  background-position: 50% 0;
  margin-top: 0.266667rem;
  padding-bottom: 0.266667rem;
}

@-webkit-keyframes discount {
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
  }

  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }
}

@keyframes discount {
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
  }

  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }
}
